  .search__form input[type="search"] {
    border: 3px solid #03b8ad;
    height: 42px;
    margin: 0;
    padding: 5px 5% 5px 5px; }
  .search__form [type="button"],
  .search__form [type="reset"],
  .search__form [type="submit"] {
    background-color: #03b8ad;
    margin: 10px 0;
    padding: 10px;
    text-decoration: underline;
    font-weight: bold; }
    @media (min-width: 23.438em) {
      .search__form [type="button"],
      .search__form [type="reset"],
      .search__form [type="submit"] {
        background-image: url("/static/img/icons/rwanda/search-icon.svg");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50%;
        padding: 0;
        height: 42px;
        width: 65px;
        margin: 0;
        text-indent: -15em;
        border-radius: 0;
        position: absolute;
        right: 0;
        top: 0; } }

.search__query-term {
  font-weight: bold; }

