.section__theme {
  width: 100%;
  padding: 2em;
  background-color: rgba(0, 0, 0, 0.5); }

.section__icon {
  width: 20%;
  vertical-align: middle;
  display: inline-block; }
  @media (min-width: 23.438em) {
    .section__icon {
      width: 50px; } }

.section__caption {
  background-color: #000;
  display: inline-block; }
  @media (min-width: 23.438em) {
    .section__caption {
      width: auto; } }
  @media (min-width: 45em) {
    .section__caption {
      position: absolute;
      left: 1em;
      top: 35%; } }

.section__title {
  color: #fff;
  font-size: 18px;
  padding: 0.313em 0.35em; }

.server-error {
  padding: 1em 7%; }

.social-media {
  overflow: auto; }
  .social-media__anchor {
    color: #fff;
    display: block; }
    .social-media__anchor:active, .social-media__anchor:hover, .social-media__anchor:focus {
      color: #fff; }
  .social-media__icon {
    display: inline-block;
    vertical-align: middle;
    box-shadow: 4px 3px 0 0 #ffe82c;
    height: 40px;
    width: 40px; }
    .social-media__icon--facebook {
      background: url("/static/img/icons/rwanda/fb.png") no-repeat center/100%;
      display: block; }
    .social-media__icon--twitter {
      background: url("/static/img/icons/rwanda/twitter.png") no-repeat center/100%;
      display: block; }
  .social-media__label {
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    padding: 0 0 0 10px; }
  @media (min-width: 45em) {
    .social-media--footer {
      flex: 1 auto;
      order: 2; } }
  .social-media-list {
    margin: 0;
    padding: 0; }
    .social-media-list__item {
      padding: 5px;
      display: block; }

