.surveys__wrapper {
  background-color: #fff;
  padding: 1em 2em; }

.tag-cloud {
  background-color: #F4F5F5;
  text-align: center;
  padding: 2em 10% 3em; }
  .tag-cloud-list {
    border-bottom: 1px solid #95989C;
    padding: 0 25% 2em;
    margin: 0; }
    .tag-cloud-list__item {
      display: inline-block;
      padding: 5px; }
  .tag-cloud__anchor {
    color: #03b8ad;
    font-size: 22px; }
    .tag-cloud__anchor:before {
      content: "#";
      display: inline; }
    .tag-cloud__anchor:hover {
      color: #02938A; }
  .tag-cloud__title {
    margin-left: -3px; }

